div {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.recipes {
    padding-top: 15px;
    padding-right: 30px;
    padding-left: 30px;
    font-family: Verdana, Geneva, sans-serif;
}

.bowl-title {
    text-align: center;
    padding: 10px 10px;
}

.sub-title {
    color: tomato;
    padding-top: 15px;
}

.video-container {
	position:relative;
	padding-bottom:56.25%;
	padding-top:30px;
	height:0;
	overflow:hidden;
}

.video-container iframe, .video-container object, .video-container embed {
	position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
}
    
#map {
  height: 91vh;
  width: 100vw;
  position: fixed;
}
.leaflet-routing-container-hide .leaflet-routing-collapse-btn {
  position: absolute;
  left: 5.5px;
  top: 0.7vh;
  width: 18px;
  height: 18px;
  background-image: url(data:image/png;base64,);
}
